import { Component, OnInit, Input } from '@angular/core';
import { MetabolightsService } from '../../../services/metabolights/metabolights.service';
import { NgRedux, select } from '@angular-redux/store';
import { FormBuilder, FormGroup, Validators} from '@angular/forms';
import * as toastr from 'toastr';

@Component({
	selector: 'mtbls-download',
	templateUrl: './download.component.html',
	styleUrls: ['./download.component.css']
})
export class DownloadComponent implements OnInit {

	@Input('value') file: string;
	
	constructor(private fb: FormBuilder, private metabolightsService: MetabolightsService) { }

	ngOnInit() {
	}

	download(){
		this.metabolightsService.downloadFile(this.file).subscribe( res => {
				toastr.success( "File downloaded successfully", "Success", {
					"timeOut": "2500",
					"positionClass": "toast-top-center",
					"preventDuplicates": true,
					"extendedTimeOut": 0,
					"tapToDismiss": false
				});
			}, err => {
				toastr.error( "Error downloading the file", "Error", {
					"timeOut": "2500",
					"positionClass": "toast-top-center",
					"preventDuplicates": true,
					"extendedTimeOut": 0,
					"tapToDismiss": false
				});
		});
	}

}
