import { Injectable } from '@angular/core';
import { CanActivate, Router, ActivatedRouteSnapshot, RouterStateSnapshot, CanActivateChild } from '@angular/router';
import { AuthService } from './services/metabolights/auth.service';
import { NgRedux } from '@angular-redux/store';
import { IAppState } from './store';
import { contentHeaders } from './services/headers';

@Injectable({
  providedIn: 'root'
})
export class AuthGuard implements CanActivate, CanActivateChild {
  constructor(private authService: AuthService, private router: Router, private ngRedux: NgRedux<IAppState>) {}

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {
    let url: string = state.url;
    return this.checkLogin(url);
  }

  canActivateChild(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {
    return this.canActivate(route, state);
  }

  checkLogin(url: string): boolean {
    let user = JSON.parse(JSON.stringify(this.ngRedux.getState().status['user']));
    if(user == null){
      let localUser = localStorage.getItem('user');
      if ( localUser != null) { 
          contentHeaders.set('user_token', JSON.parse(localUser).apiToken);
          this.ngRedux.dispatch({ type: 'SET_USER', body: {
            'user': localUser
          }})
          // localStorage.removeItem('user');
          return true; 
      }
    }else{
      contentHeaders.set('user_token', user.apiToken);
      return true;
    }
    
    // Store the attempted URL for redirecting
    this.authService.redirectUrl = url;
    
    // Navigate to the login page with extras
    this.router.navigate(['/login']);
    return false;
  }
}