import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../../services/metabolights/auth.service';
import { NgRedux, select } from '@angular-redux/store';
import { IAppState } from './../../../store';
import { MetaboLightsWSURL } from './../../../services/globals';

@Component({
	selector: 'nav-bar',
	templateUrl: './nav-bar.component.html',
	styleUrls: ['./nav-bar.component.css']
})
export class NavBarComponent implements OnInit {

	@select(state => state.study.identifier) studyIdentifier: string
	domain: string = "";
	constructor(private authService: AuthService, private ngRedux: NgRedux<IAppState>) { }

	ngOnInit() {
		this.domain = MetaboLightsWSURL['domain']
	}

	logOut() {
		this.authService.logout();
	}

	backToMetabolights() {
		this.authService.logout();
		window.location.href = this.domain;
	}
}
