import { Component, OnInit, Input } from '@angular/core';
import { NgRedux, select } from '@angular-redux/store';
import { IAppState } from './../../store';
import { MetabolightsService } from '../../services/metabolights/metabolights.service';
import { AuthService } from '../../services/metabolights/auth.service';
import { ActivatedRoute } from '@angular/router';
import { contentHeaders } from './../../services/headers';
@Component({
    selector: 'mtbls-study',
    templateUrl: './study.component.html',
    styleUrls: ['./study.component.css']
})
export class StudyComponent implements OnInit {
    private requestedStudy: string;
    private requestedTab: number = 0;
    tab: string = "descriptors";

    @select(state => state.status.user) user: any
    @select(state => state.study.identifier) studyIdentifier: string
    @select(state => state.study.title) studyTitle: string
    @select(state => state.study.abstract) studyAbstract: string
    @select(state => state.status.currentTabIndex) currentIndex: number
    @select(state => state.study.submissionDate) submissionDate: Date
    @select(state => state.study.releaseDate) releaseDate: Date
    @select(state => state.study.publications) publications: any
    @select(state => state.study.people) people: any
    @select(state => state.study.studyDesignDescriptors) studyDesignDescriptors: any[]
    @select(state => state.study.factors) factors: any[]
    @select(state => state.study.validations) validations: any
    @select(state => state.study.protocols) protocols: any[]
    @select(state => state.study.processSequence) processSequence: any[]
    @select(state => state.study.assays) assays: any[]
    @select(state => state.status.configuration) configuration: any

    constructor(private metabolightsService: MetabolightsService, private authService: AuthService, private ngRedux: NgRedux<IAppState>, private route: ActivatedRoute) {
        this.route.params.subscribe( params => {
            this.requestedStudy = params['id'];
            if(params['tab'] == 'files'){
                this.requestedTab = 5
                this.tab = "files";
            }else if(params['tab'] == 'metabolites'){
                this.requestedTab = 4
                this.tab = "metabolites";
            }else if(params['tab'] == 'assays'){
                this.requestedTab = 3
                this.tab = "assays";
            }else if(params['tab'] == 'samples'){
                this.requestedTab = 2
                this.tab = "samples";
            }else if(params['tab'] == 'protocols'){
                this.requestedTab = 1
                this.tab = "protocols";
            }else{
                this.requestedTab = 0
                this.tab = "descriptors";
            }
        });
    }

    ngOnInit() {
        if(this.ngRedux.getState().status['user'] != null){
            this.ngRedux.dispatch({ type: 'ENABLE_LOADING' })
            this.ngRedux.dispatch({ type: 'SET_LOADING_INFO', body: {
                'info': 'Loading study validations' 
            }})
            // Synchronous
            this.metabolightsService.getValidations()
            .subscribe( validations => {
                this.ngRedux.dispatch({ type: 'SET_LOADING_INFO', body: {
                    'info': 'Loading study details' 
                }})
                this.metabolightsService.getStudy(this.requestedStudy)
                .subscribe(
                    study => {
                        this.ngRedux.dispatch({ type: 'SET_CONFIGURATION', body: {
                            'configuration': study.isaInvestigation.comments
                        }})
                        this.ngRedux.dispatch({ type: 'LOAD_VALIDATION_RULES', body: {
                            'study': study,
                            'validations': validations
                        }})
                        this.ngRedux.dispatch({ type: 'SET_STUDY_IDENTIFIER', body: {
                            'study': study
                        }})
                        this.ngRedux.dispatch({ type: 'SET_STUDY_TITLE', body: {
                            'title': study.isaInvestigation.studies[0].title
                        }})
                        this.ngRedux.dispatch({ type: 'SET_STUDY_ABSTRACT', body: {
                            'description': study.isaInvestigation.studies[0].description
                        }})
                        this.ngRedux.dispatch({ type: 'SET_STUDY_SUBMISSION_DATE', body: {
                            'study': study
                        }})
                        this.ngRedux.dispatch({ type: 'SET_STUDY_RELEASE_DATE', body: {
                            'study': study
                        }})
                        this.ngRedux.dispatch({ type: 'SET_STUDY_PUBLICATIONS', body: {
                            'study': study
                        }})
                        this.ngRedux.dispatch({ type: 'SET_STUDY_PEOPLE', body: {
                            'study': study
                        }})
                        this.ngRedux.dispatch({ type: 'SET_STUDY_DESIGN_DESCRIPTORS', body: {
                            'studyDesignDescriptors': study.isaInvestigation.studies[0].studyDesignDescriptors
                        }})
                        this.ngRedux.dispatch({ type: 'SET_STUDY_FACTORS', body: {
                            'factors': study.isaInvestigation.studies[0].factors
                        }})
                        this.ngRedux.dispatch({ type: 'SET_STUDY_PROTOCOLS', body: {
                            'protocols': study.isaInvestigation.studies[0].protocols
                        }})
                        this.ngRedux.dispatch({ type: 'SET_STUDY_PROCESS_SEQUENCE', body: {
                            'processSequence': study.isaInvestigation.studies[0].processSequence
                        }})

                        let organisms = []
                        this.metabolightsService.getStudyFiles().subscribe( data => {
                          data.studyFiles.forEach( file => {
                            if(file.type.indexOf('metadata') > -1){
                              if(file.file.indexOf('s_') == 0 && file.status == 'active'){
                                this.metabolightsService.getSamplesTable(file.file).subscribe(data => { 
                                    let columns = []
                                    Object.keys(data.sampleHeader).forEach( key => {
                                        if(key.indexOf("Term Accession Number") < 0 && key.indexOf("Term Source REF") < 0){
                                            let fn = "element['"+ key +"']"
                                            columns.push({
                                                "columnDef": key,
                                                "sticky": "false",
                                                "header": key,
                                                "cell": (element) => eval(fn)
                                            })
                                        }
                                    })
                                    let displayedColumns = columns.map( a => { return a.columnDef } )
                                    displayedColumns.unshift("Select")
                                    displayedColumns.sort(function(a, b){  
                                        return parseInt(data.sampleHeader[a]) - parseInt(data.sampleHeader[b]);
                                    });
                                    data['columns'] = columns;
                                    data['displayedColumns'] = displayedColumns;
                                    data['file'] = file.file;

                                    let organisms = {}
                                    data.sampleData.rows.forEach (row =>{
                                        let organismName = row['Characteristics[Organism]'].replace(/^[ ]+|[ ]+$/g,'')
                                        let organismPart = row['Characteristics[Organism part]']
                                        let organismVariant = row['Characteristics[Variant]']
                                        if(organismName != '' && organismName.replace(" ", '') != ''){
                                            if(organisms[organismName] == null){
                                                organisms[organismName] = {
                                                    "parts" : [],
                                                    "variants" : []
                                                }
                                            }else{
                                                if(organisms[organismName]['parts'].indexOf(organismPart) < 0){organisms[organismName]['parts'].push(organismPart)}
                                                if(organisms[organismName]['variants'].indexOf(organismVariant) < 0){organisms[organismName]['variants'].push(organismVariant)}
                                            }
                                        }
                                    })
                                    this.ngRedux.dispatch({ type: 'SET_STUDY_ORGANISMS', body: {
                                        'organisms': organisms
                                    }})
                                    this.ngRedux.dispatch({ type: 'SET_STUDY_SAMPLES_TABLE', body: {
                                        'samplesTable': data
                                    }})
                                })
                              }
                            }
                            if(file.file.indexOf('a_') == 0 && file.status == 'active'){
                                this.metabolightsService.getAssayTable(file.file).subscribe(data => {
                                    let columns = []
                                    Object.keys(data.assayHeader).forEach( key => {
                                        let fn = "element['"+ key +"']"
                                        columns.push({
                                            "columnDef": key,
                                            "sticky": "false",
                                            "header": key,
                                            "cell": (element) => eval(fn)
                                        })
                                    })
                                    let displayedColumns = columns.map( a => { return a.columnDef } )
                                    displayedColumns.unshift("Select")

                                    displayedColumns.sort(function(a, b){  
                                        return parseInt(data.assayHeader[a]) - parseInt(data.assayHeader[b]);
                                    });
                                    data['columns'] = columns;
                                    data['displayedColumns'] = displayedColumns;
                                    this.ngRedux.dispatch({ type: 'ADD_STUDY_ASSAY_TABLE', body: {
                                        'assayTable': data,
                                        'assay': file.file
                                    }})

                                    this.ngRedux.dispatch({ type: 'ADD_MAF', body: {
                                        'maf': this.getMAFData(data,  file.file)
                                    }})
                                })
                            }
                          })
                        })

                        this.ngRedux.dispatch({ type: 'TOGGLE_LOADING' })
                        this.selectCurrentTab(this.requestedTab, this.tab)
                    })
            })
        }
    }

    selectCurrentTab(index, tab){
        this.ngRedux.dispatch({ type: 'SET_TAB_INDEX', body: {
            'currentTabIndex': index
        }})
        let urlSplit = window.location.pathname.replace(/\/$/, "").split("/").filter(n => n);
        if(urlSplit.length >= 3){
            if(urlSplit[urlSplit.length - 1].indexOf("MTBLS") < 0){
                urlSplit.pop();
            }
        }
        window.history.pushState("", "", window.location.origin + "/" + urlSplit.join("/") + "/" + tab);
    }

    getMAFData(assay, assayFileName){
        let mafFiles = []
        assay.assayData.rows.forEach( row => {
            if(mafFiles.indexOf(row['Metabolite Assignment File']) < 0){
                mafFiles.push(row['Metabolite Assignment File']);
            }
        })    
        let mafData = []
        mafFiles.forEach( f => {
            this.metabolightsService.getMAF(f).subscribe(data => {
                let columns = []
                Object.keys(data.mafHeader).forEach( key => {
                    let fn = "element['"+ key +"']"
                    columns.push({
                        "columnDef": key.toLowerCase().split(" ").join("_"),
                        "sticky": "false",
                        "header": key,
                        "cell": (element) => eval(fn)
                    })
                })
                let displayedColumns = columns.map( a => { return a.columnDef } )
                displayedColumns.unshift("Select")
                displayedColumns.sort(function(a, b){  
                    return parseInt(data.mafHeader[a]) - parseInt(data.mafHeader[b]);
                });
                mafData.push({ 'assay' : assayFileName, 'file': f, 'data': data.mafData.rows, 'columnOrder': {}, 'columns' : columns, 'displayedColumns': displayedColumns  });
            })
        })
        return mafData;
    }
}
