import { Component, OnInit, Input } from '@angular/core';
import { NgRedux, select } from '@angular-redux/store';

@Component({
	selector: 'mtbls-metabolites',
	templateUrl: './metabolites.component.html',
	styleUrls: ['./metabolites.component.css']
})
export class MetabolitesComponent implements OnInit {

	@Input('validations') studyValidations: any;
	@select(state => state.study.metaboliteAnnotationFiles) studyMAFFiles: any;

	mafFiles: any[] = [];

	constructor() { 
		this.studyMAFFiles.subscribe(value => {
			this.mafFiles = value
		})
	}

	ngOnInit() {
	}

	ngAfterViewChecked() {
	}

	private validationsId = 'assays';

	get validations() {
		if(this.validationsId.includes(".")){
			var arr = this.validationsId.split(".");
			let tempValidations = JSON.parse(JSON.stringify(this.studyValidations));;
			while (arr.length && (tempValidations = tempValidations[arr.shift()]));
			return tempValidations;
		}
		return this.studyValidations[this.validationsId];
	}
}
