import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { MetabolightsService } from '../../../../services/metabolights/metabolights.service';
import { MatPaginator, MatTableDataSource, MatSort} from '@angular/material';
import { NgRedux, select } from '@angular-redux/store';
import { FormBuilder, FormGroup, Validators} from '@angular/forms';
import { IAppState } from '../../../../store';
import * as toastr from 'toastr';
import { tassign } from 'tassign'; 

@Component({
	selector: 'mtbls-maf',
	templateUrl: './maf.component.html',
	styleUrls: ['./maf.component.css']
})
export class MafComponent implements OnInit {

	@Input('value') maf: any;
	@Input('assay') assay: any;
	@Input('validations') studyValidations: any;

	tableDataSource : any;

	selectedRows: any[] = []
	selectedColumns: any[] = []
	selectedCells: any[] = []

	filter: string = '';
	filters: string[] = [];
	lastRowSelection = null;
	lastColSelection = null;

	selectedColumn = null;
	selectedColumnValues = null;

	selectedCell = {};
	selectedRow = {};

	isDeleteModalOpen = false;
	isEditModalOpen = false;
	isRowEditModalOpen = false;

	isFormBusy = false;

	form: FormGroup;

	@ViewChild(MatPaginator) paginator: MatPaginator;
	@ViewChild(MatSort) sort: MatSort;


	constructor( private fb: FormBuilder, private metabolightsService: MetabolightsService, private ngRedux: NgRedux<IAppState>) { 
	}

	ngOnInit() {
		this.tableDataSource = new MatTableDataSource<any>(this.maf.data)
	}

	ngAfterViewInit() {
		this.tableDataSource.sort = this.sort;
		this.tableDataSource.paginator = this.paginator;
	}

	openRowEditModal(row){
		this.isRowEditModalOpen = true;
		this.selectedRow = row;
		this.form = this.fb.group({
			name:  [ row['metabolite_identification'] ],
			smiles:  [ row['smiles'] ],
			inchi:  [ row['smiles'] ],
			databaseId:  [ row['database_identifier'] ]
		});
	}

	closeRowEditModal(){
		this.isRowEditModalOpen = false;
	}

	search(type){
		let term = this.form.get(type).value;
		this.isFormBusy = true;
		this.metabolightsService.search(term, type).subscribe( res => {
			let resultObj = res.content[0]
			this.isFormBusy = false;
			let fields = ['name', 'smiles', 'inchi', 'databaseId']
			fields.forEach(field =>{
				if(field != term){
					this.form.get(field).setValue(resultObj[field], {emitEvent: false})
				}
			})
			this.form.markAsDirty();
		})
	}

	onKeydown(event, filterValue:string) {
		if (event.key === "Enter") {
			let data = []
			if (this.filters.indexOf(filterValue) < 0){
				this.filters.push(filterValue)
				event.target.value = ''
			}
			this.tableDataSource.filter = '';
			this.filters.forEach(f => {
				data = data.concat(this.tableDataSource.data.filter(d => this.getDataString(d).indexOf(f.toLowerCase()) > -1))
			})
			this.tableDataSource.data = data;
		}
	}

	isSelected(row, column){
		if((row && column) && this.selectedCells.length > 0){
			return this.selectedCells.filter( cell => (cell[0] == column.columnDef && cell[1] == row.index) ).length > 0
		}else if(this.selectedColumns.length == 0){
			if(this.selectedRows.indexOf(row.index) > -1){
				return true
			}
		}else if(this.selectedRows.length == 0){
			if(this.selectedColumns.indexOf(column.columnDef) > -1){
				return true
			}
		}
		return false
	}


	rowClick(row: any, event){
		this.selectedCells = []
		this.selectedColumns = []
		let entryIndex = row.index
		let rowIndex = this.selectedRows.indexOf(entryIndex)
		if(event && event.altKey){
			if( rowIndex > -1){
				this.selectedRows.splice(rowIndex, 1)
			}else{
				this.selectedRows.push(entryIndex)
			}
		}else if(event && event.shiftKey){
			let lastSelectionIndex = null;
			let lastRowIndex = -1;
			let rowNamesArray: any[] = this.maf.data.map( e => { return e.index })
			if(this.lastRowSelection){
				lastSelectionIndex = this.lastRowSelection.index
				lastRowIndex = rowNamesArray.indexOf(lastSelectionIndex)
			}else{
				lastRowIndex = 0
			}
			let currentRowIndex = rowNamesArray.indexOf(entryIndex)
			let currentSelection = []

			if(lastRowIndex > currentRowIndex){
				currentSelection = rowNamesArray.slice(currentRowIndex, lastRowIndex + 1)
			}else{
				currentSelection = rowNamesArray.slice(lastRowIndex, currentRowIndex + 1)
			}
			this.selectedRows = this.selectedRows.concat(currentSelection)
		}else{
			if( rowIndex < 0){
				this.selectedRows = [entryIndex]
			}else{
				this.selectedRows = []
			}
		}
		this.lastRowSelection = row
	}

	cellClick(row: any, column: any, event){
		if(event.altKey){
			this.selectedCells.push( [column.columnDef,row.index] )
		}else{
			this.selectedCells = [[column.columnDef,row.index]]
		}
	}

	toTitleCase(str) {
	    return str.replace(/\w\S*/g, function(txt){
	        return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();
	    });
	}

	formatCellTitle(term){
		return term.replace("_", " ")
	}

	editCell(row: any, column: any, event){
		this.isEditModalOpen = true;
		this.selectedCell['row'] = row
		this.selectedCell['column'] = column
		this.form = this.fb.group({
			cell:  [ row[column.columnDef] ],
		});
	}

	closeEditModal(){
		this.isEditModalOpen = false;
	}

	deSelect(){
		this.selectedRows = []
		this.selectedColumns = []
		this.selectedCells = []
	}

	headerClick(column: any, event){
		this.selectedCells = []
		this.selectedRows = []
		let entryIndex = column.columnDef
		let colIndex = this.selectedColumns.indexOf(entryIndex)

		if(event.altKey){
			if( colIndex > -1){
				this.selectedRows.splice(colIndex, 1)
			}else{
				this.selectedRows.push(entryIndex)
			}
		}else if(event.shiftKey){
			let lastSelectionIndex = null;
			let lastRowIndex = -1;
			let colNamesArray: any[] = this.maf.displayedColumns.map( e => { return e.columnDef })
			if(this.lastColSelection){
				lastSelectionIndex = this.lastColSelection.index
				lastRowIndex = colNamesArray.indexOf(lastSelectionIndex)
			}else{
				lastRowIndex = 0
			}
			let currentRowIndex = colNamesArray.indexOf(entryIndex)
			let currentSelection = []

			if(lastRowIndex > currentRowIndex){
				currentSelection = colNamesArray.slice(currentRowIndex, lastRowIndex + 1)
			}else{
				currentSelection = colNamesArray.slice(lastRowIndex, currentRowIndex + 1)
			}
			this.selectedColumns = this.selectedColumns.concat(currentSelection)
		}else{
			if( colIndex < 0){
				this.selectedColumns = [entryIndex]
			}else{
				this.selectedColumns = []
			}
		}
		this.lastColSelection = column
	}

	keys(object){
		return Object.keys(object);
	}

	removeFilter(filter){
		this.filters = this.filters.filter(e => e !== filter);
		this.tableDataSource.filter = '';
		if(this.filters.length > 0){
			let data = []
			this.filters.forEach(f => {
				data = data.concat(this.tableDataSource.filter(f.toLowerCase()))
			})
			console.log(data);
			this.tableDataSource.data = data;
		}else{
			this.tableDataSource.data = this.maf.data
		}
	}

	highlightFilteredRows(term){
        this.selectedRows = this.selectedRows.concat(this.maf.data.filter(f => this.getDataString(f).indexOf(term.toLowerCase()) != -1 ).map( p => p.index ));
    }

    getDataString(row){
        let rowString = ""
        Object.keys(row).forEach( prop => { if(row[prop]) {rowString = rowString + " " + row[prop] }})
        return rowString.toLowerCase();
    }

	applyFilter(filterValue: string) {
	    if(filterValue != ''){
	    	this.tableDataSource.data = this.maf.data
	    	this.tableDataSource.filter = filterValue.trim().toLowerCase();
	    }
	}

	getUnique(arr){
		return arr.filter (function (value, index, array) { 
    		return array.indexOf (value) == index;
		});
	}

	getEmptyRow(){
		let obj = tassign({}, this.maf.data[0]);
		Object.keys(obj).forEach(function (prop) {
  			obj[prop] = "";
		});
		return obj;
	}

	saveCell(){
		this.selectedCell['row'][this.selectedCell['column']['columnDef']] = this.form.get('cell').value
		this.save(this.selectedCell['row']);
	}

	saveMultipleCells(){
		this.selectedRow['metabolite_identification'] = this.form.get('name').value
		this.selectedRow['inchi'] = this.form.get('inchi').value
		this.selectedRow['database_identifier'] = this.form.get('databaseId').value
		this.selectedRow['smiles'] = this.form.get('smiles').value
		this.save(this.selectedRow);
	}

	save(data){
		this.metabolightsService.updateMAFEntry(this.maf.file, { "mafdata": [ data ]}).subscribe( res => {
				toastr.success( "MAF entry updated successfully", "Success", {
					"timeOut": "2500",
					"positionClass": "toast-top-center",
					"preventDuplicates": true,
					"extendedTimeOut": 0,
					"tapToDismiss": false
				});

				let columns = []
		        Object.keys(res.mafHeader).forEach( key => {
		          let fn = "element['"+ key +"']"
		          columns.push({
		            "columnDef": key.toLowerCase().split(" ").join("_"),
		            "sticky": "false",
		            "header": key,
		            "cell": (element) => eval(fn)
		          })
		        })
		        let displayedColumns = columns.map( a => { return a.columnDef } )
		        displayedColumns.unshift("Select")

		        displayedColumns.sort(function(a, b){  
		          return res.mafHeader[a] - res.mafHeader[b];
		        });

		        this.ngRedux.dispatch({ type: 'UPDATE_MAF_DATA', body: {
	                'data': { 'file': this.maf.file, 'data': res.mafData.rows, 'columnOrder': {}, 'columns' : columns, 'displayedColumns': displayedColumns  },
	                'assay': this.assay
	            }})
				this.rowClick(this.selectedCell['row'], null)
			}, err => {
		});
	}

	addRow(){
		this.metabolightsService.addMAFEntry(this.maf.file, { "mafdata": [this.getEmptyRow()]}).subscribe( res => {
				toastr.success( "Rows add successfully to the end of the maf sheet", "Success", {
					"timeOut": "2500",
					"positionClass": "toast-top-center",
					"preventDuplicates": true,
					"extendedTimeOut": 0,
					"tapToDismiss": false
				});


				let columns = []
		        Object.keys(res.mafHeader).forEach( key => {
		          let fn = "element['"+ key +"']"
		          columns.push({
		            "columnDef": key.toLowerCase().split(" ").join("_"),
		            "sticky": "false",
		            "header": key,
		            "cell": (element) => eval(fn)
		          })
		        })
		        let displayedColumns = columns.map( a => { return a.columnDef } )
		        displayedColumns.unshift("Select")

		        displayedColumns.sort(function(a, b){  
		          return res.mafHeader[a] - res.mafHeader[b];
		        });

		        this.ngRedux.dispatch({ type: 'UPDATE_MAF_DATA', body: {
	                'data': { 'file': this.maf.file, 'data': res.mafData.rows, 'columnOrder': {}, 'columns' : columns, 'displayedColumns': displayedColumns  },
	                'assay': this.assay
	            }})
			}, err => {
		});
	}

	deleteSelectedRows(){
		this.metabolightsService.deleteMAFEntries(this.maf.file, this.getUnique(this.selectedRows).join(",")).subscribe( res => {
			this.isDeleteModalOpen = false
			toastr.success( "Rows delete successfully", "Success", {
				"timeOut": "2500",
				"positionClass": "toast-top-center",
				"preventDuplicates": true,
				"extendedTimeOut": 0,
				"tapToDismiss": false
			});


			let columns = []
	        Object.keys(res.mafHeader).forEach( key => {
	          let fn = "element['"+ key +"']"
	          columns.push({
	            "columnDef": key.toLowerCase().split(" ").join("_"),
	            "sticky": "false",
	            "header": key,
	            "cell": (element) => eval(fn)
	          })
	        })
	        let displayedColumns = columns.map( a => { return a.columnDef } )
	        displayedColumns.unshift("Select")

	        displayedColumns.sort(function(a, b){  
	          return res.mafHeader[a] - res.mafHeader[b];
	        });

	        this.ngRedux.dispatch({ type: 'UPDATE_MAF_DATA', body: {
                'data': { 'file': this.maf.file, 'data': res.mafData.rows, 'columnOrder': {}, 'columns' : columns, 'displayedColumns': displayedColumns  },
                'assay': this.assay
            }})
		}, err => {});
	}

	closeDelete(){
		this.isDeleteModalOpen = false;
	}

	openDeleteModal(){
		this.isDeleteModalOpen = true;
	}

	keyExists(object, key){
		return object[key] != undefined
	}

	valueExists(array, value){
		return array.indexOf(value) >= 0
	}

	isObject (item) {
		return (typeof item === "object" && !Array.isArray(item) && item !== null);
	}
}
