import { Component, OnInit, Input } from '@angular/core';
import { NgRedux, select } from '@angular-redux/store';

@Component({
	selector: 'mtbls-assays',
	templateUrl: './assays.component.html',
	styleUrls: ['./assays.component.css']
})
export class AssaysComponent implements OnInit {

	@Input('validations') studyValidations: any;
	@select(state => state.study.assayTables) studyAssays;
	assays : any[] = []

	constructor() { }

	ngOnInit() {
		this.studyAssays.subscribe(value => { 
			Object.keys(value).forEach(key => {
				value[key]['name'] = key
				this.assays.push(value[key]);
			})
		})
	}

	private validationsId = 'assays';

	get validations() {
        if(this.validationsId.includes(".")){
            var arr = this.validationsId.split(".");
            let tempValidations = JSON.parse(JSON.stringify(this.studyValidations));;
            while (arr.length && (tempValidations = tempValidations[arr.shift()]));
            return tempValidations;
        }
        return this.studyValidations[this.validationsId];
    }

}
