import { Injectable } from '@angular/core';
import { contentHeaders } from './../headers';
import { AuthenticationURL } from './../globals';
import { Router } from '@angular/router';
import { Http, Response, Headers } from '@angular/http';
import { NgRedux } from '@angular-redux/store';
import { IAppState } from './../../store';
import { catchError, map, tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class AuthService{

	constructor(public router: Router, public http: Http, private ngRedux: NgRedux<IAppState>) {
	}

	isLoggedIn: boolean = false;
	redirectUrl: string = '/console';
	errorMessage: string;

	login(body): void {
		this.http.post(AuthenticationURL['login'], body, { headers: contentHeaders })
			.subscribe((response) => {
				let body = {
					"jwt" : response.headers.get("jwt"),
					"user" : response.headers.get("user")
				}
				this.http.post(AuthenticationURL['initialise'], body, { headers: contentHeaders })
					.subscribe((response) => {
						let body = JSON.parse(response.json().content);
						let user = body.owner
						this.ngRedux.dispatch({ type: 'SET_USER', body: {
				            'user': user
				        }})
				        localStorage.setItem('user', JSON.stringify(user));
				        contentHeaders.set('user_token', user.apiToken);
				        this.router.navigate([this.redirectUrl]);
					},
					error => {
						console.log(error.text());
					}
				);
			},
			error => {
				console.log(error.text());
			}
		);
	}

	logout(): void {
		localStorage.removeItem('user');
		this.router.navigate(['login']);
	}

	// Studies list
    getUserStudies() {
    	if(contentHeaders['api_token'] != ''){
	        return this.http.get(AuthenticationURL['studiesList'], { headers: contentHeaders }).pipe(
		        map(res => res.json())
	    	);
    	}
    }
}