import { Router } from '@angular/router';
import { IAppState } from './../../store';
import { NgRedux } from '@angular-redux/store';
import { Component, OnInit } from '@angular/core';
import { Http, Response, Headers } from '@angular/http';
import { contentHeaders } from './../../services/headers';
import { AuthenticationURL } from './../../services/globals';
import { AuthService } from '../../services/metabolights/auth.service';

@Component({
	selector: 'mtbls-console',
	templateUrl: './console.component.html',
	styleUrls: ['./console.component.css']
})
export class ConsoleComponent implements OnInit {

    private studies: string[] = []
    private filteredStudies: string[] = []
    loadingStudies: boolean = false;

    constructor(public router: Router, private authService: AuthService, public http: Http, private ngRedux: NgRedux<IAppState>) { }

    ngOnInit() {
        this.getUserStudies();
    }

    ngAfterContentInit() {
        //this.getUserStudies();
    }

    applyFilter(value){
        if(value != ''){
            this.filteredStudies = this.studies.filter( s => {
                if(value != ''){
                    return this.getString(s).indexOf(value) != -1;             
                }else{
                    return true;
                }
            })
        }
    }

    getString(s){
        return s.id + ' ' + s.title + ' ' + s.description 
    }

    // Studies list
    getUserStudies() {
        this.loadingStudies = true;
        this.authService.getUserStudies().subscribe(
            response => {
                this.studies = JSON.parse(response.content);
                this.filteredStudies = this.studies;
                this.loadingStudies = false;
            },
            error => {

            }
        )
    }

    logOut() {
        this.authService.logout();
    }

}
