import { Component, OnInit, Input, Inject, ViewChild, SimpleChanges } from '@angular/core';
import { FormBuilder, FormGroup, Validators} from '@angular/forms';
import { MetabolightsService } from '../../../../services/metabolights/metabolights.service';
import { Ontology } from './../../../../models/mtbl/mtbls/common/mtbls-ontology';
import { NgRedux } from '@angular-redux/store';
import { IAppState } from '../../../../store';
import * as toastr from 'toastr';
import { JsonConvert, OperationMode, ValueCheckingMode } from "json2typescript";
import { OntologyComponent } from './../../ontology/ontology.component';

@Component({
	selector: 'mtbls-design-descriptor',
	templateUrl: './design-descriptor.component.html',
	styleUrls: ['./design-descriptor.component.css']
})
export class DesignDescriptorComponent implements OnInit {

	@Input('value') descriptor: Ontology;
	@Input('validations') validations: any;
	@Input('readOnly') readOnly: boolean;

	@ViewChild(OntologyComponent) statusComponent: OntologyComponent;

	private validationsId = 'studyDesignDescriptors';

	isModalOpen: boolean = false;
	isTimeLineModalOpen: boolean = false;
	isDeleteModalOpen: boolean = false;

	form: FormGroup;
	isFormBusy: boolean = false;
	addNewDescriptor: boolean = false;

	constructor( private fb: FormBuilder, private metabolightsService: MetabolightsService, private ngRedux: NgRedux<IAppState>) { 
	}

	ngOnInit() {
		if (this.descriptor == null){
			this.addNewDescriptor = true
		}
	}

	onChanges(){
		this.form.markAsDirty();
	}

	showHistory(){
		this.isModalOpen = false
		this.isTimeLineModalOpen = true
	}

	closeHistory(){
		this.isTimeLineModalOpen = false
		this.isModalOpen = true
	}

	openModal() {
		if(!this.readOnly){			
			this.initialiseForm()
			if(this.addNewDescriptor){
				this.descriptor = null
			}
			this.isModalOpen = true
		}
	}

	initialiseForm() {
		this.isFormBusy = false;
		this.form = this.fb.group({
		});
	}

	confirmDelete(){
		this.isModalOpen = false
		this.isDeleteModalOpen = true
	}

	closeDelete(){
		this.isDeleteModalOpen = false
		this.isModalOpen = true
	}

	closeModal() {
		this.isModalOpen = false
	}

	save() {
		this.isFormBusy = true;
		if(!this.addNewDescriptor){
			this.metabolightsService.updateDesignDescriptor(this.descriptor.annotationValue, this.compileBody()).subscribe( res => {
				this.updateDesignDescriptors(res, 'Design descriptor updated.')
			}, err => {
				this.isFormBusy = false
			});
		}else{
			this.metabolightsService.saveDesignDescriptor(this.compileBody()).subscribe( res => {
				this.updateDesignDescriptors(res, 'Design descriptor saved.')
				this.statusComponent.values = []
				this.isModalOpen = false
			}, err => {
				this.isFormBusy = false
			});
		}
	}

	delete() {
		this.metabolightsService.deleteDesignDescriptor(this.descriptor.annotationValue).subscribe( res => {
				this.updateDesignDescriptors(res, 'Design descriptor deleted.')
				this.isDeleteModalOpen = false
				this.isModalOpen = false
			}, err => {
				this.isFormBusy = false
		});
	}

	compileBody() {
		let jsonConvert: JsonConvert = new JsonConvert();

		return { "studyDesignDescriptor" : jsonConvert.deserialize(this.statusComponent.values[0], Ontology)}
	}

	updateDesignDescriptors(data, message){
		this.metabolightsService.getDesignDescriptors().subscribe( res => {
			this.ngRedux.dispatch({ type: 'UPDATE_STUDY_DESIGN_DESCRIPTORS', body: {
				'studyDesignDescriptors': res.studyDesignDescriptors
			}})
		})

		this.form.markAsPristine()
		this.initialiseForm();
		this.isModalOpen = true;

		toastr.success( message, "Success", {
			"timeOut": "2500",
			"positionClass": "toast-top-center",
			"preventDuplicates": true,
			"extendedTimeOut": 0,
			"tapToDismiss": false
		});
	}

	get validation() {
		if(this.validationsId.includes(".")){
			var arr = this.validationsId.split(".");
			let tempValidations = JSON.parse(JSON.stringify(this.validations));;
			while (arr.length && (tempValidations = tempValidations[arr.shift()]));
			return tempValidations;
		}
		return this.validations[this.validationsId];
	}

	fieldValidation(fieldId) {
		return this.validation[fieldId]
	}

	getFieldValue(name){
		return this.form.get(name).value;
	}

	setFieldValue(name, value){
		return this.form.get(name).setValue(value);
	}
}
