import { IAppState } from './../../store';
import { Component, OnInit } from '@angular/core';
import { AuthService } from './../../services/metabolights/auth.service';
import { NgRedux, select } from '@angular-redux/store';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  constructor(private ngRedux: NgRedux<IAppState>, public router: Router, private authService: AuthService) { }

  ngOnInit() {
  	this.ngRedux.dispatch({ type: 'DISABLE_LOADING' })
  }

  login(event, email, secret) {
    event.preventDefault();
    let body = JSON.stringify({ email, secret });
    this.authService.login(body);
  }

}
