import { Component, OnInit, Input, Inject, OnChanges, SimpleChanges } from '@angular/core';
import { IAppState } from '../../../store';
import { NgRedux } from '@angular-redux/store';
import { MetabolightsService } from '../../../services/metabolights/metabolights.service';
import { FormBuilder, FormGroup, Validators} from '@angular/forms';
import { ValidateStudyTitle } from './title.validator';
import * as toastr from 'toastr';

@Component({
  selector: 'mtbls-title',
  templateUrl: './title.component.html',
  styleUrls: ['./title.component.css']
})
export class TitleComponent implements OnChanges, OnInit {

	@Input('value') value: string;
	@Input('validations') studyValidations: any;

  form: FormGroup;
  isFormBusy: boolean = false;

  private validationsId = 'title';

	isModalOpen: boolean = false;

	constructor( private fb: FormBuilder, private metabolightsService: MetabolightsService, private ngRedux: NgRedux<IAppState>) { 
  }

	ngOnInit() {
	}

	openModal() {
    this.initialiseForm()
		this.isModalOpen = true
	}

  initialiseForm() {
    this.isFormBusy = false;
    this.form = this.fb.group({
      title:  [ this.value, ValidateStudyTitle(this.validation)]
    });
  }

	closeModal() {
		this.isModalOpen = false
	}

  save() {
    this.isFormBusy = true;
    this.metabolightsService.saveTitle(this.compileBody(this.form.get('title').value)).subscribe( res => {
      this.ngRedux.dispatch({ type: 'SET_STUDY_TITLE', body: res})
      this.form.get('title').setValue(res.title)
      this.form.markAsPristine()
      this.isFormBusy = false;

      toastr.success('Title updated.', "Success", {
          "timeOut": "2500",
          "positionClass": "toast-top-center",
          "preventDuplicates": true,
          "extendedTimeOut": 0,
          "tapToDismiss": false
      })

    }, err => {
      this.isFormBusy = false
    });
  }

  compileBody(title) {
    return {
      'title': title,
    }
  }

  get validation() {
    return this.studyValidations[this.validationsId];
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.value != undefined){
      this.value = changes.value.currentValue
    }
    if (changes.studyValidations != undefined)
      this.studyValidations = changes.studyValidations.currentValue
  }
}
