import { Component, ViewChild, OnInit, Input, Inject, ViewChildren, AfterContentInit, QueryList, OnChanges, SimpleChanges, ChangeDetectorRef } from '@angular/core';
import { IAppState } from '../../../store';
import { tassign } from 'tassign'; 
import { MTBLSProcessSequence } from './../../../models/mtbl/mtbls/mtbls-process-sequence';
import { MTBLSSample } from './../../../models/mtbl/mtbls/mtbls-sample';
import { MTBLSColumn } from './../../../models/mtbl/mtbls/common/mtbls-column';
import { MTBLSSource } from './../../../models/mtbl/mtbls/mtbls-source';
import { MTBLSFactor } from './../../../models/mtbl/mtbls/mtbls-factor';
import { MTBLSFactorValue } from './../../../models/mtbl/mtbls/mtbls-factor-value';
import { MTBLSCharacterstic } from './../../../models/mtbl/mtbls/mtbls-characterstic';
import { Ontology } from './../../../models/mtbl/mtbls/common/mtbls-ontology';
import { FormBuilder, FormGroup, Validators} from '@angular/forms';
import { NgRedux, select } from '@angular-redux/store';
import { OntologyComponent } from './../ontology/ontology.component';
import { JsonConvert, OperationMode, ValueCheckingMode } from "json2typescript";
import { MetabolightsService } from '../../../services/metabolights/metabolights.service';
