import { Component, OnInit, Input, Inject, ViewChild, SimpleChanges } from '@angular/core';
import { FormBuilder, FormGroup, Validators} from '@angular/forms';
import { MetabolightsService } from '../../../../services/metabolights/metabolights.service';
import { Ontology } from './../../../../models/mtbl/mtbls/common/mtbls-ontology';
import { NgRedux } from '@angular-redux/store';
import { IAppState } from '../../../../store';
import * as toastr from 'toastr';
import { JsonConvert, OperationMode, ValueCheckingMode } from "json2typescript";
import { OntologyComponent } from './../../ontology/ontology.component';
import { MTBLSFactor } from './../../../../models/mtbl/mtbls/mtbls-factor';

@Component({
	selector: 'mtbls-factor',
	templateUrl: './factor.component.html',
	styleUrls: ['./factor.component.css']
})
export class FactorComponent implements OnInit {

	@Input('value') factor: MTBLSFactor;
	@Input('validations') validations: any;
	@Input('isDropdown') isDropdown: boolean = false;

	@ViewChild(OntologyComponent) factorTypeComponent: OntologyComponent;

	private validationsId = 'factors.factor';

	isModalOpen: boolean = false;
	isTimeLineModalOpen: boolean = false;
	isDeleteModalOpen: boolean = false;

	form: FormGroup;
	isFormBusy: boolean = false;
	addNewFactor: boolean = false;

	constructor( private fb: FormBuilder, private metabolightsService: MetabolightsService, private ngRedux: NgRedux<IAppState>) {
	}

	ngOnInit() {
		if (this.factor == null){
			this.addNewFactor = true
		}
	}

	onChanges(){
		this.form.markAsDirty();
	}

	showHistory(){
		this.isModalOpen = false
		this.isTimeLineModalOpen = true
	}

	closeHistory(){
		this.isTimeLineModalOpen = false
		this.isModalOpen = true
	}

	openModal() {
		if(this.addNewFactor){
			this.factor = new MTBLSFactor()
		}
		this.initialiseForm()
		this.isModalOpen = true
	}

	initialiseForm() {
		this.isFormBusy = false;
		this.form = this.fb.group({
			factorName:  [ this.factor.factorName],
		});
	}

	confirmDelete(){
		this.isModalOpen = false
		this.isDeleteModalOpen = true
	}

	closeDelete(){
		this.isDeleteModalOpen = false
		this.isModalOpen = true
	}

	closeModal() {
		this.isModalOpen = false
	}

	save() {
		this.isFormBusy = true;
		if(!this.addNewFactor){
			this.metabolightsService.updateFactor(this.factor.factorName, this.compileBody()).subscribe( res => {
				this.updateFactors(res, 'Factor updated.')
			}, err => {
				this.isFormBusy = false
			});
		}else{
			this.metabolightsService.saveFactor(this.compileBody()).subscribe( res => {
				this.updateFactors(res, 'Factor saved.')
				this.isModalOpen = false
			}, err => {
				this.isFormBusy = false
			});
		}
	}

	delete() {
		this.metabolightsService.deleteFactor(this.factor.factorName).subscribe( res => {
				this.updateFactors(res, 'Factor deleted.')
				this.isDeleteModalOpen = false
				this.isModalOpen = false
			}, err => {
				this.isFormBusy = false
		});
	}

	updateFactors(data, message){
		this.metabolightsService.getFactors().subscribe( res => {
			this.ngRedux.dispatch({ type: 'UPDATE_STUDY_FACTORS', body: {
				'factors': res.factors
			}})
		})

		this.form.markAsPristine()
		this.initialiseForm();
		this.isModalOpen = true;

		toastr.success( message, "Success", {
			"timeOut": "2500",
			"positionClass": "toast-top-center",
			"preventDuplicates": true,
			"extendedTimeOut": 0,
			"tapToDismiss": false
		});
	}

	compileBody() {
		let mtblsFactor = new MTBLSFactor();
		mtblsFactor.factorName = this.getFieldValue('factorName');
		mtblsFactor.comments = []
		let jsonConvert: JsonConvert = new JsonConvert();
		mtblsFactor.factorType = jsonConvert.deserialize(this.factorTypeComponent.values[0], Ontology)
		return { "factor" : mtblsFactor.toJSON()}
	}


	get validation() {
		if(this.validationsId.includes(".")){
			var arr = this.validationsId.split(".");
			let tempValidations = JSON.parse(JSON.stringify(this.validations));;
			while (arr.length && (tempValidations = tempValidations[arr.shift()]));
			return tempValidations;
		}
		return this.validations[this.validationsId];
	}

	fieldValidation(fieldId) {
		return this.validation[fieldId]
	}

	getFieldValue(name){
		return this.form.get(name).value;
	}

	setFieldValue(name, value){
		return this.form.get(name).setValue(value);
	}

}
